import React from 'react';
import { QRCodeCanvas } from 'qrcode.react';
import jsPDF from 'jspdf';
import logo from '../../../assets/img/brand/nsut-logo-1800x1800.png';

const PDFViewer = ({ admit_card_data, admit_card_data2 }) => {
  const handleQRCodeClick = () => {
    window.open(
      `data:application/json;charset=utf-8,${encodeURIComponent(
        JSON.stringify(admit_card_data)
      )}`,
      "_blank"
    );
  };

  const handleDownloadClick = () => {
    const doc = new jsPDF();
    
    // Header
    doc.addImage(logo, 'PNG', 10, 10, 20, 20);
    doc.setFontSize(16);
    doc.setFont(undefined, 'bold');
    doc.text('NETAJI SUBHAS UNIVERSITY OF TECHNOLOGY', doc.internal.pageSize.width / 2, 15, { align: 'center' });
    doc.setFontSize(12);
    doc.setFont(undefined, 'normal');
    doc.text('Training and Placement Cell', doc.internal.pageSize.width / 2, 22, { align: 'center' });
    doc.setFontSize(10);
    doc.text('AZAD HIND FAUJ MARG, SECTOR-3, DWARKA, NEW DELHI - 110078', doc.internal.pageSize.width / 2, 28, { align: 'center' });

    // Horizontal line
    doc.line(10, 35, doc.internal.pageSize.width - 10, 35);

    // Admit Card title
    doc.setFontSize(14);
    doc.setFont(undefined, 'bold');
    doc.text('ADMIT CARD', doc.internal.pageSize.width / 2, 45, { align: 'center' });

    // QR Code
    const canvas = document.getElementById('qr-code');
    const imgData = canvas.toDataURL('image/png');
    doc.addImage(imgData, 'PNG', 10, 55, 50, 50);

    // Admit card details
    doc.setFontSize(10);
    doc.setFont(undefined, 'normal');
    let yPosition = 55;
    let leftColumn = 70;
    let rightColumn = doc.internal.pageSize.width / 2 + 10;
    
    Object.entries(admit_card_data2).forEach(([key, value], index) => {
      const column = index % 2 === 0 ? leftColumn : rightColumn;
      
      // Check if we need to start a new page
      if (yPosition > doc.internal.pageSize.height - 30) {
        doc.addPage();
        yPosition = 20;
      }
      
      doc.setFont(undefined, 'bold');
      doc.text(`${key}:`, column, yPosition);
      doc.setFont(undefined, 'normal');
      
      // Handle long values by wrapping text
      const splitValue = doc.splitTextToSize(value.toString(), 80);
      doc.text(splitValue, column, yPosition + 5);
      
      // Adjust yPosition based on the number of lines in the value
      const lineHeight = 5;
      const valueHeight = splitValue.length * lineHeight;
      
      if (index % 2 !== 0) {
        yPosition += Math.max(valueHeight + 10, 20); // Ensure minimum spacing between rows
      }
    });

    // Footer
    doc.setFontSize(8);
    doc.text('This is an official admit card. Please bring it to the examination center.', doc.internal.pageSize.width / 2, doc.internal.pageSize.height - 10, { align: 'center' });

    doc.save('admit_card.pdf');
  };

  return (
    <div
      style={{
        width: "200px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-end",
      }}
    >
      <div style={{ width: "200px" }} onClick={handleQRCodeClick}>
        <QRCodeCanvas
          id="qr-code"
          value={JSON.stringify(admit_card_data)}
          bgColor="#FFFFFF"
          fgColor="#000000"
          size={200}
          includeMargin={false}
          style={{ border: 0 }}
        />
      </div>
      <div>
        <button onClick={handleDownloadClick} style={buttonStyle}>
          Hall Card
        </button>
      </div>
    </div>
  );
};

const buttonStyle = {
  backgroundColor: "#EE3444",
  color: "white",
  border: "none",
  width: "100%",
  padding: "10px 25px",
  fontSize: "16px",
  borderRadius: "5px",
  cursor: "pointer",
  transition: "background-color 0.3s",
};

export default PDFViewer;
